import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemText from '@material-ui/core/ListItemText';
import ListItemAvatar from '@material-ui/core/ListItemAvatar';
import Avatar from '@material-ui/core/Avatar';
import Divider from '@material-ui/core/Divider';
import SchoolIcon from '@material-ui/icons/School';

const useStyles = makeStyles(theme => ({
  root: {
    width: '100%',
    maxWidth: 360,
    backgroundColor: theme.palette.background.paper,
  },
}));

export default function InsetDividers() {
  const classes = useStyles();

  return (
    <List className={classes.root}>
      <ListItem>
        <ListItemAvatar>
          <Avatar>
           <SchoolIcon htmlColor={'black'}/>
          </Avatar>
        </ListItemAvatar>
        <ListItemText primary="BTS SIO" secondary="2019 - 2020" />
      </ListItem>
      <Divider variant="inset" component="li" />
      <ListItem>
        <ListItemAvatar>
          <Avatar>
          <SchoolIcon htmlColor={'black'} />
          </Avatar>
        </ListItemAvatar>
        <ListItemText primary="Licences Philosophie" secondary="2017 - 2018" />
      </ListItem>
      <Divider variant="inset" component="li" />
      <ListItem>
        <ListItemAvatar>
          <Avatar>
          <SchoolIcon htmlColor={'black'} />
          </Avatar>
        </ListItemAvatar>
        <ListItemText primary="Licences Sociologie" secondary="2016 - 2017" />
      </ListItem>
      <Divider variant="inset" component="li" />
      <ListItem>
        <ListItemAvatar>
          <Avatar>
          <SchoolIcon htmlColor={'black'} />
          </Avatar>
        </ListItemAvatar>
        <ListItemText primary="DUT Informatique" secondary="2015 - 2016" />
      </ListItem>
      <Divider variant="inset" component="li" />
      <ListItem>
        <ListItemAvatar>
          <Avatar>
          <SchoolIcon htmlColor={'black'} />
          </Avatar>
        </ListItemAvatar>
        <ListItemText primary="BAC STI2D" secondary="2014 - 2015" />
      </ListItem>
    </List>
  );
}


