import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import Typography from '@material-ui/core/Typography';

const useStyles = makeStyles(theme => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
    height: '20vw'
  },
  fluid: {
      width: 'auto',
      height: '18vw'
  },
  hr : {
    fontFamily: 'Poppins'
  }
}));

export default function Skills() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
      <Grid container spacing={3}>
        <Grid item xs>
          <Paper className={classes.paper}>
            <Typography variant="h5" component="h5" className={classes.hr}>
                Dveloppement
            </Typography>
              <img className={classes.fluid} src="/img/undraw_web_developer_p3e5.png" alt="Dev" />
          </Paper>
        </Grid>
        <Grid item xs>
          <Paper className={classes.paper}>
            <Typography variant="h5" component="h5" className={classes.hr}>
                Esprit d'quipe
            </Typography>
          <img className={classes.fluid} src="/img/undraw_team_spirit_hrr4.png" alt="Team" />
          </Paper>
        </Grid>
        <Grid item xs>
          <Paper className={classes.paper}>
            <Typography variant="h5" component="h5" className={classes.hr}>
                Crativit
            </Typography>
             <img className={classes.fluid} src="/img/undraw_new_ideas_jdea.png" alt="Creative" />
          </Paper>
        </Grid>
      </Grid>
    </div>
  );
}


