import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Link from '@material-ui/core/Link';
import GitHubIcon from '@material-ui/icons/GitHub';
import LinkedInIcon from '@material-ui/icons/LinkedIn';
import TwitterIcon from '@material-ui/icons/Twitter';
import FacebookIcon from '@material-ui/icons/Facebook';

const useStyles = makeStyles(theme => ({
    logo : {
        margin: theme.spacing(3),
        opacity: 0.75,
        '&:hover': {
            opacity: 1
          }
    }
}));

export default function Logos(props) {

    const classes = useStyles();

    return (
        <div>
            <Link className={classes.logo} href="https://github.com/maelnilba" target="_blank" rel="noreferrer" color="inherit">
            <GitHubIcon fontSize="large" htmlColor={props.clrs}>GitHub</GitHubIcon>
            </Link>
            <Link className={classes.logo} href="https://www.linkedin.com/in/mal-rioux-612854189/" target="_blank" rel="noreferrer" color="inherit">
            <LinkedInIcon fontSize="large" htmlColor={props.clrs}>LinkedIn</LinkedInIcon>
            </Link>
            <Link className={classes.logo} href="https://twitter.com/SewMael" target="_blank" rel="noreferrer" color="inherit">
            <TwitterIcon fontSize="large" htmlColor={props.clrs}>Twitter</TwitterIcon>
            </Link>
            <Link className={classes.logo} href="https://www.facebook.com/mael.1515" target="_blank" rel="noreferrer" color="inherit">
            <FacebookIcon fontSize="large" htmlColor={props.clrs}>Facebook</FacebookIcon>
            </Link>  
        </div>     
    );
}


