import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Avatar from '@material-ui/core/Avatar';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Paper from '@material-ui/core/Paper';
import Grid from '@material-ui/core/Grid';
import SaveIcon from '@material-ui/icons/Save';
import MediaCart from './competences';
import Skills from './skills';
import Link from '@material-ui/core/Link';
import {Footer, FooterSection, FooterLinkList} from 'react-mdl';
import Logos from './logos';
import InsetDividers from './cursus';


const useStyles = makeStyles(theme => ({
  root: {
    textAlign: 'center',
  },
  presentation : {
    background: 'url(https://origarti.fr/wp-content/uploads/2019/06/enseigner-developpement-web-workshop-cover.jpg) no-repeat center center fixed',
    width: '100vw',
    height:'95vh',
    display: 'table-cell',
    verticalAlign: 'middle'
  },
  papers : {
    width: '100%',
    height: '95vh',
    margin: 'auto',
    padding: '10px',
    boxShadow: 'none'
  },
  Avatar: {
    margin: 'auto',
    width: 300,
    height: 300
  },
  h: {
    color: 'white',
    fontFamily: 'Poppins',
    margin: '20px',
    textShadow: '1px 1px 2px white, 0 0 1em silver, 0 0 0.2em black'
  },
  hr: {
    fontFamily: 'Poppins',
    margin: '20px'
  },
  hs:{
    fontFamily: 'Poppins'
  },
  button: {
    margin: theme.spacing(1),
  },
  paper: {
    padding: theme.spacing(2),
    textAlign: 'center',
    color: theme.palette.text.secondary,
  },
  noshadow : {
    boxShadow: 'none'
  },
  card: {
    maxWidth: 345,
  },
  media: {
    height: 140,
  },
  grid: {
    flexGrow: 1,
  },
  marge: {
    margin: '20px'
  },
  langages : {
    height: '100vh',
    textAlign: 'center'
  },
  logos : {
    position: 'relative',
    bottom: '-20vh'
  },
  footer : {
    background: 'black',
    width: '100vw',
    display: 'table-cell',
    verticalAlign: 'middle'
  },
  footlink: {
    '&:hover': {
      cursor: 'pointer',
      color: 'white'
    }
  },
  cursus: {
    width: '500px',
    margin: '0px auto'
    }
}));


export default function Landing() {

  const classes = useStyles();

    return(
         
        <div className={classes.root}>
          <div id="presentation" className={classes.presentation}>
                  <Typography variant="h1" component="h2" className={classes.h}>
                    Rioux Mal
                    </Typography>
                    <Typography variant="h3" component="h3" className={classes.h}>
                    Dveloppeur Web
                  </Typography>
                <div className={classes.root}>
                  <Button variant="contained" size="large" onClick={() => {document.getElementById('resume').scrollIntoView(); }}>En savoir plus</Button>
                </div>
                <div className={classes.logos}>
                  <Logos clrs="#FFF"/>
                </div>
          </div>
          <div id="resume">
            <Paper className={classes.paper}>
              <Avatar alt="profilepic" src="https://i.imgur.com/WGzPFD5.jpg" className={classes.Avatar} />
              <Typography variant="h2" component="h2" className={classes.hr}>
                Bio
              </Typography>
              <Typography component="p" className={classes.hs}>
                Je m'appelle Rioux Mal, 22 ans, originaire de Lyon
              </Typography>
              <Typography component="p" className={classes.hs}>
              Interress par l'informatique depuis le plus jeune ge, j'envisage de devenir un dveloppeur web fullstack
              </Typography>
              <Typography component="p" className={classes.hs}>
              J'ai commenc par une anne de DUT Informatique de 2016  2017  IUT2 situ  Grenoble puis j'ai repris mes tudes en BTS SIO option SLAM  Sciences-u Lyon
              </Typography>
              <div className={classes.cursus}>
                <InsetDividers/>
              </div>  
              <Button variant="contained" size="large" onClick={() => {document.getElementById('competences').scrollIntoView(); }}>Aller plus loin</Button>
              <Button
                variant="contained"
                color="primary"
                size="large"
                className={classes.button}
                startIcon={<SaveIcon />}
              >
                Tlcharger le CV
              </Button>
            </Paper>
          </div>
          <div id="competences">
          <Paper className={classes.papers}>
              <Typography variant="h2"  className={classes.hr}>
                Comptences
              </Typography>
              <div className={classes.marge}>
                <Skills/>
              </div>           
             <Button variant="contained" size="large" onClick={() => {document.getElementById('langages').scrollIntoView(); }}>Voir les langages</Button>
          </Paper>
          </div>
          <div id="langages" className={classes.langages}>
            <Paper className={classes.noshadow}>
              <Typography variant="h2"  className={classes.hr}>
                Langages
              </Typography>
              <div className={classes.grid}>
                <Grid container spacing={2}>
                    <Grid item xs={4}>
                        <MediaCart name="JavaScript" value="90" frm="P5, Socket.io, React.js"/>
                    </Grid>
                    <Grid item xs={4}>
                        <MediaCart name="HTML" value="80"/>
                    </Grid>
                    <Grid item xs={4}>
                        <MediaCart name="CSS" value="70" frm="Sass, Bootstrap"/>
                    </Grid>
                    <Grid item xs={4}>
                        <MediaCart name="Python" value="40" frm="Pygame"/>
                    </Grid>
                    <Grid item xs={4}>
                        <MediaCart name="C++" value="45"/>
                    </Grid>
                    <Grid item xs={4}>
                        <MediaCart name="Kotlin" value="30"/>
                    </Grid>
                </Grid>
              </div>
            </Paper>
          </div>
          <Footer size="mini" className={classes.footer}>
              <FooterSection type="left" logo="Prsentation">
                  <FooterLinkList>
                  <Link className={classes.footlink} onClick={() => {document.getElementById('presentation').scrollIntoView(); }}>
                    Acceuil
                  </Link>
                  <Link className={classes.footlink} onClick={() => {document.getElementById('resume').scrollIntoView(); }}>
                    Bio
                  </Link>
                  <Link className={classes.footlink} onClick={() => {document.getElementById('competences').scrollIntoView(); }}>
                    Comptences
                  </Link>
                  <Link className={classes.footlink} onClick={() => {document.getElementById('langages').scrollIntoView(); }}>
                    Langages
                  </Link>
                  </FooterLinkList>
              </FooterSection>
           </Footer>
        </div>     
    )
}



