import React from 'react';
import './App.css';
import { Layout, Header, Navigation, Drawer, Content } from 'react-mdl';
import Main from './components/main';
import { Link } from 'react-router-dom';
import { makeStyles } from '@material-ui/core/styles';
import Logos from './components/logos';

const useStyles = makeStyles(theme => ({
  root: {
    background: 'black'
  },
  logos : {
    position: 'absolute',
    bottom: '0'
  }
}));


 export default function App() {
  
  const classes = useStyles();
    return (
      <div>
            <Layout fixedHeader fixedTabs>
                <Header className={classes.root} title={<Link to="/" onClick={() => {
                      if (window.location.pathname === '/') {
                        document.getElementById('presentation').scrollIntoView(); 
                      }    
                    }
                  } style={{textDecoration: 'none', color: 'white'}}>Rioux Mal</Link>}>
                    <Navigation>
                        <Link to="/veille">Veilles</Link>
                        <Link to="/projects">Projets</Link>
                        <Link to="/contact">Contact</Link>
                    </Navigation>
                </Header>
                <Drawer title={<Link style={{textDecoration: 'none', color: 'black'}} to="/">Rioux Mal</Link>}>
                    <Navigation>
                      <Link to="/veille">Veilles</Link>
                      <Link to="/projects">Projets</Link>
                      <Link to="/contact">Contact</Link>
                      <div className={classes.logos}>
                        <Logos clrs="#000"/>
                      </div>
                    </Navigation>
                </Drawer>
                <Content>
                    <Main/>
                </Content>
            </Layout>
      </div>
    );
}




